import React, { Component, } from 'react';
import { Row, Col, Form, Select, Input, } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;



@Form.create({ name: 'formScholarshipApproval' })


class SearchBar extends Component {
  state = {
  };

  handleReset = () => {
    this.props.form.resetFields();
  }

  handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  }


  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <Form
        className="ant-advanced-search-form"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <Col span={8} >
            <Form.Item label='Hệ'>
              {getFieldDecorator('system1', {
              })(
                <Select>
                  <Option value="1">Đại học chình quy</Option>
                  <Option value="2">Cao đẳng chính quy</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Khoa'>
              {getFieldDecorator('system2', {
              })(
                <Select>
                  <Option value="1">Công nghệ thực phẩm</Option>
                  <Option value="2">Kế toán</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Khóa'>
              {getFieldDecorator('system3', {
              })(
                <Select>
                  <Option value="1">Khóa 1</Option>
                  <Option value="2">Khóa 2</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='CN'>
              {getFieldDecorator('system4', {
              })(
                <Select>
                  <Option value="1">Kế toán kiểm toán</Option>
                  <Option value="2">Kế toán kho</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Học kỳ'>
              {getFieldDecorator('system5', {
              })(
                <Select>
                  <Option value="1">Học kỳ 1</Option>
                  <Option value="2">Học kì 2</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Năm học'>
              {getFieldDecorator('system6', {
              })(
                <Select>
                  <Option value="1">2017-2018</Option>
                  <Option value="2">2018-2019</Option>
                  <Option value="3">2019-2020</Option>
                  <Option value="4">2020-2021</Option>
                </Select>
              )
              }
            </Form.Item>
          </Col>

          <Col span={8} >
            <Form.Item label='Mã sinh viên'>
              {getFieldDecorator('system11', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Họ tên'>
              {getFieldDecorator('system12', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Loại quyết định'>
              {getFieldDecorator('system13', {
              })(
                <Select >
                  <Option value="1">Học tiếp</Option>
                  <Option value="2">Thôi học</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar



